                <!-- <a href="tel:917093621076"><div class="contactPhone contactMeans">+91 70 936 21076</div></a> -->
                <a href="https://linkedin.in/abdulshaik"><div class="contactLinkedIn contactMeans">linkedin.in/abdulshaik</div></a>
                <a href="https://twitter.com/abdulhimself"><div class="contactTwitter contactMeans">twitter.com/abdulhimself</div></a>
            </div>
        </div>
        <div class="rightContainer">
            <div class="divForm">
                <div class="row firstRow">
                    <div class="divInputBox">
                        <label class="divInputLabel">Fullname</label>
                        <input type="text" class="inpBox" ref="name" v-model="name" :class="isNameError?'contactError':''" @keyup="validateForm"/>
                    </div>
                    <div class="divInputBox divSecondBox">
                        <label class="divInputLabel">E-mail</label>
                        <input type="text" class="inpBox" v-model="mail" :class="isMailError?'contactError':''" @keyup="validateForm" />
                    </div>
                </div>
                <div class="row">
                    <div class="divInputBox">
                        <label class="divInputLabel">Subject</label>
                        <input type="text" class="inpBox" v-model="subject" :class="isSubjectError?'contactError':''" @keyup="validateForm" />
                    </div>
                </div>
                <div class="row">
                    <div class="divInputBox">
                        <label class="divInputLabel">Message</label>
                        <textarea class="inpBox" rows="7" v-model="message" :class="isMessageError?'contactError':''" @keyup="validateForm" />
                    </div>
                </div>
                <div class="row">
                    <!-- <a :href="mailLink"> -->
                        <div class="submitBtn" @click="submitMessage">GET IN TOUCH</div>
                        <div class="errorForm" :class="errors.length==0?'VH':''">Enter valid {{errors.join(", ")}} details</div>
                    <!-- </a> -->
                </div>
            </div>
        </div>
    </div>
