            <a href="https://www.linkedin.com/in/skabdul/"><span class="contactMeans contactLinkedin"></span></a>
            <a href="https://github.com/skabdul/"><span class="contactMeans contactGitHub"></span></a>
        </div>
    </div>
</div>
</template>

<script>
import Grid from "./Grid.vue";
import axios from "axios";
import { API_LINK } from "../ApiLink.js";

export default {
  name: "Contact",
  components: { Grid },
  data() {
    return {
      name: "",
      mail: "",
      subject: "",
      message: "",
      isNameError: false,
      isMailError: false,
      isSubjectError: false,
      isMessageError: false,
      errors: []
    };
  },
  computed: {
    mailLink: function() {
      return (
        "mailto:abdul.shaik@baseblock.io?subject=" +
        this.subject +
        "&body=" +
        this.name +
        " with email id <a href='" +
        this.mail +
        "'>" +
        this.mail +
        "</a> contacted you. <br/> Message is : " +
        this.message
      );
    }
  },
  methods: {
    submitMessage() {
      this.validateForm();
      if (this.errors.length == 0) {
        console.log("Submit");
        axios({
          method: "post",
          url: API_LINK + "send",
          data: {
            receiver: "abdul.shaik@baseblock.io",
            subject: this.name + " has contacted you on your portfolio website",
            emailBody:
              "Hi Abdul, <br/>You have a new message on your portfolio website. <br/>Details are as follows: <br/> Name: " +
              this.name +
              "<br/>mail: <a href=mailto:" +
              this.mail +
              ">" +
              this.mail +
              "</a><br/>Subject: " +
              this.subject +
              "<br/>Message: " +
              this.message +
              "<br/><br/> Regards,<br/> Your Friendly Bot."
          }
        }).then(function(response) {
          console.log(response);
        });
      }
    },
    validateForm() {
      this.isNameError = false;
      this.isMailError = false;
      this.isSubjectError = false;
      this.isMessageError = false;
      this.errors = [];
      if (this.name.trim() == "") {
        this.isNameError = true;
        this.errors.push("Name");
      }
      if (!this.validateEmail(this.mail)) {
        this.isMailError = true;
        this.errors.push("e-mail");
      }
      if (this.subject.trim() == "") {
        this.isSubjectError = true;
        this.errors.push("subject");
      }
      if (this.message.trim() == "") {
        this.isMessageError = true;
        this.errors.push("message");
      }
    },
    validateEmail(email) {
      var re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
      return re.test(String(email).toLowerCase());
    }
  }
};
</script>

